import Vue from "vue";
import Router from "vue-router";
import Dashboard from "@/components/Dashboard";
import NewEmployee from "@/components/NewEmployee";
import ViewEmployee from "@/components/ViewEmployee";
import EditEmployee from "@/components/EditEmployee";
import Login from "@/components/Login";
import Register from "@/components/Register";
import firebase from "firebase";

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: "/",
      name: "dashboard",
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/login",
      name: "login",
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: "/register",
      name: "register",
      component: Register,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: "/new",
      name: "new-employee",
      component: NewEmployee,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/edit/:employee_id",
      name: "edit-employee",
      component: EditEmployee,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/:employee_id", // :employee_id is the parameter we bind to this link
      name: "view-employee",
      component: ViewEmployee,
      meta: {
        requiresAuth: true
      }
    }
  ]
});

// nav guards
// to prevent an unlogged in dude from getting into nono places
router.beforeEach((to, from, next) => {
  // check for requiredAuth guard!
  // so this is like if the route requires authentication = true
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // check if NOT logged in
    if (!firebase.auth().currentUser) {
      // send them to login page if not logged in
      next({
        path: "/login",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // prceed to the route by calling next if logged in already
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    if (firebase.auth().currentUser) {
      // basically if they click register or login and they're already
      // logged in, take them to the dashboard instead
      next({
        path: "/",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // click register or login and they're not logged in, take them there!
      next();
    }
  } else {
    // so if there's no requiresauth or requiresguest or anything
    // proceed to route
    next();
  }
});

// now export the router
export default router;



