<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li
        v-for="emp in this.employees"
        v-bind:key="emp.id"
        class="collection-item"
      >
        <div class="chip">
          {{ emp.dept }}
        </div>
        {{ emp.employee_id }}: {{ emp.name }}

        <router-link
          class="secondary-content"
          v-bind:to="{
            name: 'view-employee',
            params: { employee_id: emp.employee_id },
          }"
          ><i class="fa fa-eye"></i
        ></router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>
  
<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      employees: [], // what we want to fill when we sql get
    };
  },
  created() {
    // want to fetch from sql when we run it every time
    db.collection("Employees")
      .orderBy("employee_id")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position,
          };
          this.employees.push(data);
        });
      });
  },
};
</script>


